import React from 'react';
import ReactDOM from 'react-dom/client'; //  ReactDOM API

//  Home Assistant 
interface HomeAssistant {
  states: object;
  services: object;
  config: object;
  themes: object;
  selectedTheme: object;
  panels: object;
  dockedSidebar: boolean;
  moreInfoEntityId: string;
  user: object;
}

// 
export default function HassPanel(WrappedComponent: any) {
  return class extends HTMLElement {
    private _renderScheduled: any;
    private _hass!: HomeAssistant;
    private _showMenu!: boolean;
    private _narrow!: boolean;
    private _panel: any;
    private root: ShadowRoot | null = null;
    private reactRoot: ReactDOM.Root | null = null;

    constructor() {
      super();
      this._renderScheduled = null;

      //  setter
      const props: any = {};
      ['hass', 'showMenu', 'narrow', 'panel'].forEach((prop) => {
        const key = `_${prop}`;
        (this as any)[key] = null;
        props[prop] = {
          set(value: any) {
            (this as any)[key] = value;
            this._render();
          }
        };
      });
      Object.defineProperties(this, props);
    }

    connectedCallback() {
      this.root = this.attachShadow({ mode: 'open' });
      this.reactRoot = ReactDOM.createRoot(this.root); //  React root
    }

    disconnectedCallback() {
      if (this.reactRoot) {
        this.reactRoot.unmount();
      }
    }

    _render() {
      if (this._renderScheduled !== null || !this.root || !this.reactRoot) return;

      this._renderScheduled = Promise.resolve().then(() => {
        this._renderScheduled = null;
        this.reactRoot!.render(
          React.createElement(WrappedComponent, {
            hass: this._hass,
            showMenu: this._showMenu,
            narrow: this._narrow,
            panel: this._panel,
          })
        );
      });
    }
  };
}

